import {Component, ViewChild} from '@angular/core';
import {ProductService} from "../../services/product.service";
import {ActivatedRoute} from "@angular/router";
import {CartModalComponent} from "../../components/cart-modal/cart-modal.component";

@Component({
	moduleId: module.id,
	selector: 'product-item',
	templateUrl: 'product-item.component.html',
	providers: [ProductService]
})
export class ProductItemComponent {
	product: any;
	sub: any;
	orderQuanlity = 1;

	@ViewChild(CartModalComponent) cartModal: CartModalComponent;

	constructor(private productService: ProductService, private route: ActivatedRoute) {

	}


	ngOnInit() {
		this.sub = this.route.params.subscribe(params => {
			let slug = params['slug'];
			console.log('key--------', slug);
			this.productService.getBySlug(slug).subscribe(product => {
				this.product = product;
			})
		});
	}

	addQuanlity = () => {
		if (this.orderQuanlity < this.product.qty) {
			this.orderQuanlity += 1;
		}
	};

	subtractQuanlity = () => {
		if (this.orderQuanlity > 1) {
			this.orderQuanlity -= 1;
		}
	};

	addCartModal = () => {
		this.product['orderQuanlity'] = this.orderQuanlity;
		this.cartModal.showModal(this.product);
	};

	ngOnDestroy() {
		this.sub.unsubscribe();
	}
}



