import {Component} from '@angular/core';
import {CategoryService} from '../../services/category.service';
import {Category} from '../../models/Category';
import {Tag} from "../../models/Tag";
import {TagService} from "../../services/tag.service";
import {Product} from "../../models/Product";
import {ProductService} from "../../services/product.service";
@Component({
  moduleId: module.id,
  selector: 'my-nav',
  templateUrl: 'nav.component.html',
  providers: [CategoryService, TagService, ProductService]
})

export class NavComponent {
  categories: Category[];
  tags: Tag[];
  bestProducts: Product[];

  constructor(private categoryService: CategoryService, private tagService: TagService, private productService: ProductService) {
  }
  ngOnInit(){
    this.categoryService.getAll().subscribe(categories => {
      this.categories = categories;
    });
    this.tagService.getAll().subscribe(tags => {
      this.tags = tags;
    });
    this.productService.getAll('bestSeller', '').subscribe(bestProducts => {
      this.bestProducts = bestProducts;
    })
  }
}



