import {Component} from '@angular/core';
import {CartService} from "../../services/cart.service";
import {Product} from "../../models/Product";
import {Router} from "@angular/router";
@Component({
	selector: 'block-cart',
	templateUrl: 'block-cart.component.html',
	providers: [CartService]
})

export class BlockCartComponent {
	cart: Product[];
	isShowCart = false;
	totalPriceCart: number;

	constructor(private cartService: CartService, private router: Router) {
		this.cart = this.cartService.getAll();
		this.totalPriceCart = this.cartService.getTotalPriceCart();
		this.cartService.getSubject().subscribe(message => {
			this.cart = this.cartService.getAll();
			this.totalPriceCart = this.cartService.getTotalPriceCart();
			console.log('message ', message);
		});
	}

	toggleShow = () => {
		this.isShowCart = !this.isShowCart;
	};
	removeCart = (slug) => {
		this.cartService.removeCart(slug);
	};

	checkout = () => {
		this.isShowCart = false;
		this.router.navigate(['/checkout']);

	}


}



