import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {Subject} from 'rxjs/Subject';
import {Observable} from "rxjs/Observable";

@Injectable()
export class CartService {
	private subject = new Subject<any>();

	constructor() {
	}

	getAll() {
		let cart;
		if (!localStorage['cart']) {
			cart = [];
		} else {
			cart = JSON.parse(localStorage['cart']);
		}
		return cart;
	}
	
	removeAll(){
		localStorage.setItem('cart','');
		this.subject.next({text: 'change'});
	}

	addCart(product) {
		let cart: any[] = [];
		if (!localStorage.getItem('cart')) {
			cart = [product];
		} else {
			cart = JSON.parse(localStorage['cart']);
			let index = this.checkExistProduct(cart, product.slug);
			if (index == -1) {
				cart.push(product);
			} else {
				let qty = cart[index].qty;
				let orderQuanlity = cart[index].orderQuanlity;
				orderQuanlity = orderQuanlity + product.orderQuanlity;
				if (orderQuanlity <= qty) {
					cart[index].orderQuanlity = orderQuanlity;
				}
			}
		}
		localStorage.setItem('cart', JSON.stringify(cart));
		this.subject.next({text: 'change'});
	}

	getTotalPriceCart() {
		let totalPriceCart = 0;

		if (localStorage['cart']) {
			let cart = JSON.parse(localStorage['cart']);
			for (let i = 0; i < cart.length; i++) {
				totalPriceCart += cart[i].orderQuanlity * cart[i].price;
			}
		}
		return totalPriceCart;
	}

	getTotalQuanlity() {
		let totalQuality = 0;

		if (localStorage['cart']) {
			let cart = JSON.parse(localStorage['cart']);
			for (let i = 0; i < cart.length; i++) {
				totalQuality += cart[i].orderQuanlity;
			}
		}
		return totalQuality;
	}

	removeCart(slug) {
		if (localStorage['cart']) {
			let cart = JSON.parse(localStorage['cart']);
			let index = this.checkExistProduct(cart, slug);
			cart.splice(index, 1);
			localStorage.setItem('cart', JSON.stringify(cart));
			this.subject.next({text: 'change'});
		}
	}

	subtractQuanlityCart(slug, orderQuanlity) {
		let cart: any[] = [];
		cart = JSON.parse(localStorage['cart']);
		let index = this.checkExistProduct(cart, slug);
		orderQuanlity = cart[index].orderQuanlity - orderQuanlity;
		if (orderQuanlity >= 1) {
			cart[index].orderQuanlity = orderQuanlity;
			localStorage.setItem('cart', JSON.stringify(cart));
			this.subject.next({text: 'change'});
			return true;
		}
		return false;


	}

	addQuanlityCart(slug, orderQuanlity) {
		let cart: any[] = [];
		cart = JSON.parse(localStorage['cart']);
		let index = this.checkExistProduct(cart, slug);
		orderQuanlity = cart[index].orderQuanlity + orderQuanlity;
		if (orderQuanlity <= cart[index].qty) {
			cart[index].orderQuanlity = orderQuanlity;
			localStorage.setItem('cart', JSON.stringify(cart));
			this.subject.next({text: 'change'});
			return true;
		}
		return false;
	}

	getSubject(): Observable<any> {
		return this.subject.asObservable();
	}

	checkExistProduct(cart, slug) {
		for (let i = 0; i < cart.length; i++) {
			if (cart[i].slug == slug) {
				return i;
			}
		}
		return -1;
	}

	getBySlug(slug) {
		if (localStorage['cart']) {
			let cart = JSON.parse(localStorage['cart']);
			let index = this.checkExistProduct(cart, slug);
			return cart[index];
		}
		return null;
	}
}



