import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';

import {routing} from './app.routes';

import {AppComponent} from './app.component';
import {FooterComponent} from "./components/footer/footer.component";
import {HomeComponent} from "./pages/home/home.component";
import {HeaderComponent} from "./components/header/header.component";
import {LoginComponent} from "./components/login/login.component";
import {BlockCartComponent} from "./components/block-cart/block-cart.component";

import {SearchComponent} from "./pages/search/search.component";
import {ProductItemComponent} from "./pages/product-item/product-item.component";
import {CategoryComponent} from "./pages/category/category.component";
import {ProductTypeComponent} from "./pages/product-type/product-type.component";
import {QuickViewComponent} from "./components/quick-view/quick-view.component";
import {ListProductComponent} from "./components/list-product/list-product.component";
import {ListProductPaginationComponent} from "./components/list-product-pagination/list-product-pagination.component";
import {CartModalComponent} from "./components/cart-modal/cart-modal.component";
import {CommentComponent} from "./components/comment/comment.component";
import {NavComponent} from "./components/nav/nav.component";
import {HttpClient} from "./services/http-client";


import {SlideModule} from "./components/slide/slide.module";

@NgModule({
  declarations: [
    AppComponent, HeaderComponent, FooterComponent, HomeComponent, LoginComponent, BlockCartComponent, SearchComponent, CategoryComponent, ProductItemComponent, ProductTypeComponent, QuickViewComponent, ListProductComponent, ListProductPaginationComponent, CartModalComponent, CommentComponent, NavComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule, routing, SlideModule
  ],
  providers: [HttpClient],
  bootstrap: [AppComponent]
})
export class AppModule {
}



