import {Component, Input} from '@angular/core';
import {AuthService} from "../../services/auth.service";
import {PostCommentService} from "../../services/post-comment.service";
import {ProductCommentService} from "../../services/product-comment.service";
@Component({
	moduleId: module.id,
	selector: 'comment-pagination',
	templateUrl: 'comment.component.html',
	providers: [AuthService, PostCommentService, ProductCommentService]
})

export class CommentComponent {

	content: string;

	isLogin = false;

	totalComments = [];
	comments = [];

	@Input() type: string;
	@Input() slug: string;


	constructor(private authService: AuthService, private postCommentService: PostCommentService, private productCommentService: ProductCommentService) {
		this.isLogin = this.authService.isLogin();

	}

	ngOnInit() {
		if (this.type == 'product') {
			this.productCommentService.getAll(this.slug).subscribe(comments => {
				this.totalComments = comments;
				this._initStartEnd();
			});
		} else {
			this.postCommentService.getAll(this.slug).subscribe(comments => {
				this.totalComments = comments;
				this._initStartEnd();
			});
		}

	}

	onComment = () => {
		if (this.type == 'product') {
			this.productCommentService.add(this.slug, {'content': this.content}).subscribe(comment => {
				this.content = "";
				this.totalComments.push(comment);
				this._initStartEnd();
			}, error => {
				console.log('error', error);
				if (error.status == 401) {
					this.authService.removeUser();
					this.isLogin = false;
				}
			});
		} else {
			this.postCommentService.add(this.slug, {'content': this.content}).subscribe(comment => {
				this.content = "";
				this.totalComments.push(comment);
				this._initStartEnd();
			}, error => {
				if (error.status == 401) {
					this.authService.removeUser();
					this.isLogin = false;
				}
			});
		}

	};

	onCancel = () => {
		this.content = "";
	};

	currentPage = 1;
	pageLimit = 3;
	start = 1;
	end = 1;
	pageCount = 1;

	setCurrentPage = (page) => {
		this.currentPage = page;
		this._initStartEnd();
	};
	previousPage = () => {
		if (this.currentPage != 1) {
			this.currentPage -= 1;
			this._initStartEnd();
		}
	};
	nextPage = () => {
		if (this.currentPage < this.pageCount) {
			this.currentPage += 1;
			this._initStartEnd();
		}
	};
	showAll = () => {
		this.pageLimit = this.totalComments.length;
		this.currentPage = 1;
		this._initStartEnd();

	};

	_initStartEnd() {
		let count = this.totalComments.length;
		this.end = this.currentPage * this.pageLimit;
		this.start = this.end - this.pageLimit + 1;
		if (this.end > count) {
			this.end = count;
		}
		this.comments = this._range(this.totalComments, this.start, this.end);
	}

	_range(array, start, end) {
		start -= 1;
		let results = [];
		for (let x = start; x < end; x++) {
			let item = array[x];
			if (item) {
				results.push(item);
			}
		}
		return results;
	}

	getPageSize() {
		let input = [];
		this.pageCount = Math.ceil(this.totalComments.length / this.pageLimit);
		for (let i = 1; i <= this.pageCount; i++) {
			input.push(i);
		}
		return input;
	};


}



