import {Component, Input, ViewChild} from '@angular/core';
import {Product} from "../../models/Product";
import {CartModalComponent} from "../cart-modal/cart-modal.component";
import {QuickViewComponent} from "../quick-view/quick-view.component";
declare var $: any;
@Component({
	selector: 'list-product-pagination',
	templateUrl: 'list-product-pagination.component.html',
})

export class ListProductPaginationComponent {
	@Input() totalProducts: Product[];
	@ViewChild(CartModalComponent) cartModal: CartModalComponent;
	@ViewChild(QuickViewComponent) quickView: QuickViewComponent;
	typeDisplay = 'grid';
	products: Product[];
	currentPage = 1;
	pageLimit = 3;
	start = 1;
	end = 1;
	pageCount = 1;

	orderBy = '';

	constructor() {

	}


	ngOnChanges() {
		console.log('aaa');
		this._initStartEnd();
	}

	addCart = (product) => {
		product['orderQuanlity'] = 1;
		this.cartModal.showModal(product);
	};

	changeNumberPage(pageLimit) {
		this.pageLimit = pageLimit;
		this.currentPage = 1;
		this._initStartEnd();
	}

	changeOrderBy(orderBy) {
		console.log('aaa', orderBy);
		if (orderBy === 'price:asc') {
			this.totalProducts.sort(function (a: Product, b: Product) {
				console.log(a.price - b.price);
				return a.price - b.price;
			});
		} else if (orderBy === 'price:desc') {
			this.totalProducts.sort(function (a: Product, b: Product) {
				return b.price - a.price;
			});
		} else if (orderBy === 'name:asc') {
			this.totalProducts.sort(function (a: Product, b: Product) {
				if (a.name > b.name) {
					return 1;
				} else {
					return -1;
				}
			});
		}else{
			this.totalProducts.sort(function (a: Product, b: Product) {
				if(a.name > b.name) {
					return -1;
				} else {
					return 1;
				}
			});
		}
		console.log('aaaa',this.totalProducts);
		this._initStartEnd();
	}

// 	$scope.totalProducts = products;
// } else if (order === 'price:desc') {
// 	products.sort(function(b, a) {
// 		return a.price - b.price;
// 	});
//
// 	$scope.totalProducts = products;
// } else if (order === 'name:desc') {
// 	products.sort(function(a, b) {
// 		if(a.name > b.name) {
// 			return -1;
// 		} else {
// 			return 1;
// 		}
// 	});
//
// 	$scope.totalProducts = products;
// } else if (order === 'name:asc') {
// 	products.sort(function(b, a) {
// 		if(a.name > b.name) {
// 			return -1;
// 		} else {
// 			return 1;
// 		}
// 	});
//
//
//
//
// }

		quickViewShow = (product) => {
			this.quickView.showModal(product);
		};

		display = (type) => {
			this.typeDisplay = type;
		};

		setCurrentPage = (page) => {
			this.currentPage = page;
			this._initStartEnd();
		};
		previousPage = () => {
			if (this.currentPage != 1) {
				this.currentPage -= 1;
				this._initStartEnd();
			}
		};
		nextPage = () => {
			if (this.currentPage < this.pageCount) {
				this.currentPage += 1;
				this._initStartEnd();
			}
		};
		showAll = () => {
			this.pageLimit = this.totalProducts.length;
			this.currentPage = 1;
			this._initStartEnd();

		};

		_initStartEnd()
		{
			let count = this.totalProducts.length;
			this.end = this.currentPage * this.pageLimit;
			this.start = this.end - this.pageLimit + 1;
			if (this.end > count) {
				this.end = count;
			}
			this.products = this._range(this.totalProducts, this.start, this.end);
			this._initAnimation();

		}

		_range(array, start, end)
		{
			start -= 1;
			let results = [];
			for (let x = start; x < end; x++) {
				let item = array[x];
				if (item) {
					results.push(item);
				}
			}
			return results;

		}

		getPageSize()
		{
			let input = [];
			this.pageCount = Math.ceil(this.totalProducts.length / this.pageLimit);
			for (let i = 1; i <= this.pageCount; i++) {
				input.push(i);
			}
			return input;
		}
		;

		_initAnimation()
		{
			$('.ajax_block_product').hover(function () {
				let $self = $(this);
				$self.find('.replace-2x').animate({
					opacity: 0
				});
				$(this).find('.hover-image').animate({
					opacity: 1
				});
			}, function () {
				let $self = $(this);
				$self.find('.hover-image').animate({
					opacity: 0
				});
				$(this).find('.replace-2x').animate({
					opacity: 1
				});
			});
		}


	}



