import {Component, ViewChild} from '@angular/core';
import {Product} from "../../models/Product";
import {CartModalComponent} from "../cart-modal/cart-modal.component";

declare var $: any;
declare var FB: any;

@Component({
	// moduleId: module.id,
	selector: 'quick-view',
	templateUrl: 'quick-view.component.html',
})
export class QuickViewComponent {
	$element;
	product: Product;

	imageDefault = "";
	quanlity = 1;

	@ViewChild(CartModalComponent) cartModal: CartModalComponent;


	constructor() {

	}

	ngOnInit() {
		this.$element = $('#quick-view');

	}

	public showModal(product) {
		this.product = product;
		this.imageDefault = this.product['image']['url'];
		this.$element.modal('show');
		console.log(product);
	}


	addQuanlity = () => {
		console.log('aaaa');
		if (this.quanlity < this.product.qty) {
			this.quanlity += 1;
		}
	};

	subtractQuanlity = () => {
		if (this.quanlity > 1) {
			this.quanlity -= 1;
		}
	};

	addCartModal = (product) => {
		product['orderQuanlity'] = this.quanlity;
		this.cartModal.showModal(product);
	};


	changeImage = (src) => {
		this.imageDefault = src;
	};


	isScrollDown: boolean = false;
	isScrollUp: boolean = true;
	scrollDown = () => {
		let scrollTop = $('.quick-view#thumbs_list_frame').scrollTop();
		scrollTop -= 81;
		$('.quick-view#thumbs_list_frame').animate({scrollTop: scrollTop}, 700, 'linear');

		if ((scrollTop - 80) <= 0) {
			this.isScrollDown = false;

		}
		this.isScrollUp = true;

	};
	scrollUp = function () {

		let scrollTop = $('.quick-view#thumbs_list_frame').scrollTop();
		scrollTop += 81;
		$('.quick-view#thumbs_list_frame').animate({scrollTop: scrollTop}, 700, 'linear');

		let height = $('.quick-view#thumbs_list_frame').offset().top;
		console.log(height);

		if (scrollTop >= height) {
			this.isScrollUp = false;

		}
		this.isScrollDown = true;

	};


	shareDialog = () => {
		FB.ui({
			method: 'share',
			display: 'popup',
			href: window.location.protocol + "//" + window.location.hostname + "/products/" + this.product.slug,
		});

	};

}



